import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { EditorsComponent } from './editors.component';


import { ControlReportDownloadComponent } from './control-report-download/control-report-download.component';


import { ControlReportCreateComponent } from './control-report-create/control-report-create.component';
import { UploadEvidenceComponent } from './upload-evidence/upload-evidence.component';
import { ControlReportEditComponent } from './control-report-edit/control-report-edit.component';

const routes: Routes = [{
  path: '',
  component: EditorsComponent,
  children: [{
    path: 'control-report-download',
    component: ControlReportDownloadComponent,
  },{
    path:'control-report-create',
    component:ControlReportCreateComponent,
  },
  
  {
    path:'upload-evidence',
    component:UploadEvidenceComponent,
  }
],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EditorsRoutingModule { }

export const routedComponents = [
  EditorsComponent,
  ControlReportCreateComponent,
  ControlReportDownloadComponent,
  UploadEvidenceComponent,
  ControlReportEditComponent
  
 
];



