import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { Evidence } from '../../../@core/data/entities/evidence';
import { EvidenceService } from '../../../@core/data/services/evidence.service';

@Component({
  selector: 'upload-evidence',
  templateUrl: './upload-evidence.component.html',
  styleUrls: ['./upload-evidence.component.scss']
})
export class UploadEvidenceComponent implements OnInit {

  evidencecaptureddate:Date = new Date();
  evidence:Evidence= new Evidence();
  screenshot:string[];
  dateSettings = {
      bigBanner: true,
      timePicker: false,
      format: 'MM-dd-yyyy',
      defaultOpen: false
  };

  ngOnInit(){}
  form: FormGroup;
  loading: boolean = false;

  @ViewChild('fileInput') fileInput: ElementRef;

  constructor(private fb: FormBuilder, private evidenceService:EvidenceService) {
    this.createForm();
    }

  createForm() {
    this.form = this.fb.group({
      transactionreferenceno: ['', Validators.required],
      userid: ['', Validators.required],
      machinename: ['', Validators.required],
      evidencecaptureddate:null,
      screenshot: null
    });
  }

  onFileChange(event) {
    let reader = new FileReader();
    if(event.target.files && event.target.files.length > 0) {
      let file = event.target.files[0];
      console.log(file);
      console.log('upload');
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.form.get('screenshot').setValue({
          filename: file.name,
          filetype: file.type,
          value: reader.result.split(',')[1]
        })
      };
    }
  }

  onSubmit() {
    const formModel = this.form.value;
    this.loading = true;
    // this.http.post('apiUrl', formModel)
    this.evidence.transactionReferenceNo=this.form.value.transactionreferenceno;

    this.evidence.capturedDateTime=new Date(this.form.value.evidencecaptureddate).toISOString();

    this.evidence.userId=this.form.value.userid;
    this.evidence.machineName=this.form.value.machinename;
    this.evidence.screenshots.push(this.form.value.screenshot.value);
    this.evidence.noOfEvidences=1;
    
    
    this.evidenceService.saveEvidence(this.evidence)
    .subscribe(x=>{
      console.log(x);
      alert('saved');
    });

    // setTimeout(() => {
    //   console.log(this.evidence);
    
    //   this.loading = false;
    // }, 1000);
  }

  clearFile() {
    this.form.get('screenshot').setValue(null);
    this.fileInput.nativeElement.value = '';
  }

}



