import { Component, Input, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { Column } from 'ng2-smart-table/lib/data-set/column';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { Evidence } from '../../../@core/data/entities/evidence';

import { EvidenceService } from '../../../@core/data/services/evidence.service';
import { ControlService } from '../../../@core/data/services/control.service';
import { SearchCriteria, FilterColumns } from '../../../@core/data/entities/searchCriteria';
import { TranslateService } from '@ngx-translate/core';
import { ControlReport } from '../../../@core/data/entities/control-report';
import { Router } from '@angular/router';


@Component({
  selector: 'control-report-edit',
  templateUrl: './control-report-edit.component.html',
  styles: [`
    nb-card {
      transform: translate3d(0, 0, 0);
    }
  `],
})

export class ControlReportEditComponent implements OnInit , OnChanges {  

  
@Input() data;
translation:any={};
settings:{};
reportCreationDate : string;
 
  ngOnInit(){

    this.translate.use(localStorage.getItem('lang'));
  
}
ngOnChanges(changes: SimpleChanges) {
   console.log(changes.data.currentValue);
   this.reportCreationDate=new Date(changes.data.currentValue).toISOString();
   this.FetchControlReports();
}
  public renderValue;
  @Input() eviedence : Evidence;

  @Input() value :any;
  totalRecords:number;
  targetData: any=[];
  controlReport: ControlReport;

  constructor(
    private translate: TranslateService,
    private service: SmartTableService,
    private controlService:ControlService,
   
  ) {
    
    translate.addLangs(["en", "fr"]);
  translate.setDefaultLang('en');
  let browserLang = translate.getBrowserLang();
  translate.use(browserLang.match(/en|fr/) ? browserLang : 'en');
    // const data = this.service.getData();
    // this.source.load(data);

  this.LoadTranslation();

  }

  LoadTranslation(): any {
    this.translate.getTranslation(localStorage.getItem('lang')).subscribe((res:string)=> {
      this.translation=res; 
      
      // console.log(res);

      this.settings = {
        
       
          delete: {
          confirmDelete: true,
          deleteButtonContent: '<i class="nb-trash"></i>',
          
        },
        edit: {
          confirmSave: true,
          editButtonContent: '<i class="nb-edit"></i>',
          saveButtonContent: '<i class="nb-checkmark"></i>',
          cancelButtonContent: '<i class="nb-close"></i>',
        },
        add: {
          addButtonContent: '<i class="nb-plus"></i>',
          createButtonContent: '<i class="nb-checkmark"></i>',
          cancelButtonContent: '<i class="nb-close"></i>'
         },
        columns: {
         transactionReferenceNo: {
            title: this.translation.TRANSACTION_REFERENCE_NO,
            type: 'string',
          },
          entity: {
            title: this.translation.ENTITY,
            type: 'string',
          },
          // createdDate: {
          //   title: 'Created Date',
          //   type: 'string',
          // },
          operator: {
            title: this.translation.OPERATOR,
            type: 'string',
          },
          qualityCheck: {
            title: this.translation.PASS+'/'+this.translation.FAIL,
            type: 'html',
            editor: {
              type: 'list',
              config: {
                list: [
                  { value: 'Pass', title: 'Pass' },
                  { value: 'Fail', title: 'Fail' }
                ]
              }
            }
          },
          qualityCheckFailCategory: {
            title: this.translation.QUALITY_CHECK_FAIL_CATEGORY,
            type: 'html',
            editor: {
              type: 'list',
              config: {
                list: [
                  { value: 'Wrongly released at L1', title: 'Wrongly released at L1' },
                  { value: 'Wrongly released from Pending', title: 'Wrongly released from Pending' },
                  { value: 'Wrongly released instead of Pending',title: 'Wrongly released instead of Pending'},
                  { value: 'Wrongly rejected instead of Pending',title: 'Wrongly rejected instead of Pending'},
                  { value: 'Responsibility Miss',title: 'Responsibility Miss'},
                  { value: 'Incorrect Comment',title: 'Incorrect Comment'},
                  { value: 'Screenshot not captured',title: 'Screenshot not captured'}

                  // { value: 'Wrongly released at L1', title: this.translation.WRONGLY_RELEASED_AT_L1 },
                  // { value: 'Wrongly released from Pending', title: this.translation.WRONGLY_RELEASED_FROM_PENDING  },
                  // { value: 'Wrongly released instead of Pending',title: this.translation.WRONGLY_RELEASED_INSTEAD_OF_PENDING },
                  // { value: 'Wrongly rejected instead of Pending',title: this.translation.WRONGLY_REJECTED_INSTEAD_OF_PENDING},
                  // { value: 'Responsibility Miss',title: this.translation.RESPONSIBILITY_MISS },
                  // { value: 'Incorrect Comment',title: this.translation.INCORRECT_COMMENT },
                  // { value: 'Screenshot not captured',title: this.translation.SCREENSHOT_NOT_CAPTURED }
                ]
              }
            }
          },
          comments: {
            title:this.translation.COMMENTS,
            type: 'string',
          }
        },
      };
    
    
    });
    
  }

  FetchControlReports()
  {
    var search:SearchCriteria = this.searchCriteria()
    this.controlService.FetchControlReports(search as SearchCriteria)
    .subscribe(x=>{
      console.log(x);
      this.totalRecords=x.items.length;
      this.source.load(x.items);
      this.targetData=x.items;
    });
  }


  dateSettings = {
        bigBanner: true,
        timePicker: false,
        format: 'MM-dd-yyyy',
        defaultOpen: false
    };
 
 

  source: LocalDataSource = new LocalDataSource();

  onDownload() {
    var search:SearchCriteria = this.searchCriteria()
    this.controlService.FetchControlReports(search as SearchCriteria)
    .subscribe(x=>{
      console.log(x);
      this.totalRecords=x.items.length;
      this.source.load(x.items);
      this.targetData=x.items;
      var fileName="controlreport";

      if (this.targetData.constructor != Array) {
          console.error('Can not export error type data to excel.');
          return;
      }
      alasql('SELECT * INTO XLSX("' + fileName + '.xlsx",{headers:true}) FROM ?', [this.targetData]);
    });
   
  }
  
  onSearch()
  {
    var search:SearchCriteria = this.searchCriteria()
    this.controlService.FetchControlReports(search as SearchCriteria)
    .subscribe(x=>{
      console.log(x);
      this.totalRecords=x.items.length;
      this.source.load(x.items);
      this.targetData=x.items;
    });

  }

  searchCriteria()
  {
    var search:SearchCriteria = new SearchCriteria();
    search.filterColumns=[];
    var filter:FilterColumns = new  FilterColumns();
    filter.columnName='startdate';
    //filter.value=this.ConvertDateFormat(this.startDate);
    filter.value=this.reportCreationDate;
    search.filterColumns.push(filter);
    filter= new FilterColumns();
    filter.columnName='enddate';
    filter.value=this.reportCreationDate;
    search.filterColumns.push(filter);
    return search;
  }

   ConvertDateFormat(str) {
    var date = new Date(str),
        mnth = ("0" + (date.getMonth()+1)).slice(-2),
        day  = ("0" + date.getDate()).slice(-2);
    return [ date.getFullYear(), mnth, day ].join("-");
}

  onDeleteConfirm(event) {
    if (window.confirm('Are you sure you want to delete?')) {
      event.confirm.resolve();
      console.log(event.data.id);
      this.controlService.DeleteControlReport(event.data.id)
      .subscribe(x=>{
        console.log(x);  

      });
      
    } else {
      event.confirm.reject();
    }
  }

  onSaveConfirm(event) {
    if (window.confirm('Are you sure you want to save?')) {
      event.newData['name'] += ' + added in code';
      event.confirm.resolve(event.newData);
      this.controlReport=event.newData;
      //this.controlReport.createddate = new Date(event.newData.createdDate).toISOString();
      this.controlService.UpdateControlReport(this.controlReport)
      .subscribe(x=>{
        console.log(x);
       
      });
    } else {
      event.confirm.reject();
    }
    
   
  }

  onCreateConfirm(event)  {
    if (window.confirm('Are you sure you want to create?')) {
      event.newData['name'] += ' + added in code';
      event.confirm.resolve(event.newData);
    } else {
      event.confirm.reject();
    }

    //this.notification.makeToast();
  }

  onCustom(event) {
    // alert(`Custom event '${event.action}' fired on row : ${event.data.id}`)
  }

  
}



