import { NgModule } from '@angular/core';
import { CKEditorModule } from 'ng2-ckeditor';

import { ThemeModule } from '../../@theme/theme.module';

import { EditorsRoutingModule, routedComponents } from './editors-routing.module';
import { ControlReportDownloadComponent } from './control-report-download/control-report-download.component';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { ToasterModule } from 'angular2-toaster';




import { AngularDateTimePickerModule } from 'angular2-datetimepicker';
import { SmartTableService } from '../../@core/data/smart-table.service';


import { EvidenceService } from '../../@core/data/services/evidence.service';


import { ControlReportCreateComponent } from './control-report-create/control-report-create.component';


import { ControlService } from '../../@core/data/services/control.service';
import 'hammerjs'; // <------ mandatory dependency for angular-modal-gallery
import 'mousetrap'; // <------ mandatory dependency for angular-modal-gallery
import { ModalGalleryModule } from 'angular-modal-gallery'; // 

import * as alasql from 'alasql';


import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';



// **************************************************************************



@NgModule({
  imports: [
    ToasterModule,
    ThemeModule,
    EditorsRoutingModule,
    CKEditorModule,
    Ng2SmartTableModule,
    AngularDateTimePickerModule,
    ModalGalleryModule,
    TranslateModule.forRoot({loader: {
      provide: TranslateLoader,
      useFactory: HttpLoaderFactory,
      deps: [HttpClient],
    
    
    }})
    
    
  ],
  declarations: [
    ...routedComponents,
    

    
    
    
  ],
  entryComponents: [
    
    ],
    providers: [
    
      SmartTableService,
      EvidenceService,
      ControlService
    ],
})
export class EditorsModule { }

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}



