import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';

import { SearchCriteria } from '../entities/searchCriteria';

import { ControlReport, ControlReportListing } from '../entities/control-report';
import { environment } from '../../../../environments/environment';
//import { MessageService } from './message.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class ControlService {

  

  constructor(
    private http: HttpClient ){}
    

  SaveControlReport (control: ControlReport): Observable<ControlReport[]> {
    return this.http.post<any>(`${environment.baseUrl}SaveControlReport`, control, httpOptions)
    .pipe(
      tap((test: any) => this.log(`Saving Control Report`)),
      catchError(this.handleError<any>('ControlReport'))
    );    
  }

  FetchControlReports (searchCriteria: SearchCriteria): Observable<ControlReportListing> {
    var x;
     return this.http.post<ControlReportListing>(`${environment.baseUrl}ControlReports`, searchCriteria, httpOptions)
     .pipe(
      tap((test: ControlReportListing) => this.log(`fetching Control Reports`)),
      catchError(this.handleError<ControlReportListing>('Fetching control reports'))
    );
  }


  UpdateControlReport (control: ControlReport): Observable<any> {
    return this.http.post<any>(`${environment.baseUrl}UpdateControlReport`, control, httpOptions)
    .pipe(
      tap((test: any) => this.log(`Updating Control Report`)),
      catchError(this.handleError<any>('ControlReport'))
    );    
  }

  DeleteControlReport(id:string): Observable<any>{
    return this.http.delete<any>(`${environment.baseUrl}DeleteControlReport/${id}`,httpOptions)
    .pipe( tap((test: any) => this.log(`Deleting Control Report`)),
    catchError(this.handleError<any>('ControlReport')));
  }


  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a HeroService message with the MessageService */
  private log(message: string) {
    console.log(message);
  }
}




