export class SearchCriteria {
    page: number;
    size: number;
    filterColumns:FilterColumns[];
    sortColumns:SortColumns[];
   
 } 
 export class FilterColumns
 {
     columnName:string;
     value:string;
     condition:string;
 }
 export class SortColumns
 {
     columnName:string;
     direction:string;
 }

 


